.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.heroes_wrap {
  display: flex; 
  column-gap: 10px; 
  max-width: 90%;
  margin: 40px auto; 
  flex-wrap: wrap;}

.heroes_wrap h2 {width: 100%}
#tarifs-wrap {
  display: flex;
  column-gap: 3px;
  max-width: 90%;
  margin: 40px auto;
  flex-wrap: wrap;
  margin-bottom: 150px;
}
#tarifs-wrap h2 {
  margin-bottom: 50px;
  width: 100%;
}